.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.action-container {
  /* margin: 20px; */
  border: 2px solid white;
  border-radius: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 600px;
}

.timer-container {
  /* border: 2px solid white;
  border-radius: 80px; */
  width: 300px;
  margin: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.timer-container h1 {
  font-size: 25px;
  margin: 15px;
  text-align: center;
}
.action-btn span {
  font-size: 30px;
  margin: 0 10px;
  width: 80px;
  text-align: center;
}
.action-btn button {
  font-size: 35px;
  margin: 0 10px;
  margin-bottom: 15px;
  height: 45px;
  width: 45px;
  text-align: center;
  border: 1px solid white;
  border-radius: 5px;
}

.clock-container {
  border: 5px solid white;
  border-radius: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 10px auto;
  width: 200px;
  height: 190px;
}

.clock-container h1 {
  margin: 0;
  font-size: 30px;
}
.clock-container span {
  font-size: 45px;
  margin: 10px 0;
}

.clock-btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

.clock-btn button {
  height: 50px;
  width: 150px;
  margin: 10px;
  font-size: 20px;
  border: 2px solid white;
  border-radius: 40px;
  cursor: pointer;
}
#chg-btn {
  height: 50px;
  width: 315px;
  margin: auto;
  margin-bottom: 20px;
  font-size: 20px;
  border: 2px solid white;
  border-radius: 50px;
  cursor: pointer;
}
button:hover {
  box-shadow: 0px 0px 10px rgb(178 0 255);
}
button {
  background-color: whitesmoke;
  color: black;
}

@media screen and (max-width: 630px) and (min-width: 400px) {
  body {
    /* background-color: rgb(0, 255, 115); */
  }

  .action-container {
    flex-direction: column;
    width: 450px;
    border-radius: 20px;
    margin-bottom: 20px;
  }
  .action-btn {
    margin: 0 10px;
  }
}
@media screen and (max-width: 450px) {
  body {
    /* background-color: rgb(255, 57, 57); */
  }

  .action-container {
    flex-direction: column;
    width: 300px;
    margin: 0 auto;
    border: 0px;
    margin-bottom: 20px;
  }

  .clock-btn button {
    width: 130px;
  }
  #chg-btn {
    width: 275px;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

